.PokemonCard {
    flex: 0 0 23%;
    margin: 30px 1% 40px 1%;
    height: 260px;
    box-shadow: 0px 2px 5px 0px rgba(31,78,95,1);
    border-radius: 8px;
    box-sizing: border-box;
    transition: 0.3s;
    cursor: pointer;
    position: relative;
    background: linear-gradient(45deg, rgb(198, 140, 198) 50%, rgb(174, 222, 150) 50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #212529;
    &:hover {
      box-shadow: 0px 2px 10px 0px rgba(31,78,95,1);
      transform: translateY(-10px);
    }

    img {
      position: absolute;
      width: 100px;
      height: 100px;
      top: -50px;
      filter: drop-shadow(5px 5px 5px #222);
      image-rendering: pixelated;
      vertical-align: middle;
    }
}

.PokemonName {
  text-transform: capitalize;
  font-size: 30px;
  padding: 3px 10px;
  margin: 50px 0 0 0;
}

.PokemonId {
  font-size: 20px;
}

.Info {
  width: 60%;
  margin: 1.2em auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.WeightHeight {
  display: block;
  padding: 6px;
  font-size: 20px;
}

.Type {
  font-size: 18px;
}

.Details {
  margin: auto 0;
  font-size: 30px;
  opacity: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -30%);
  transition: 0.3s;
}

.DetailInfo {
  opacity: 1;
  transition: 0.3s;
}

.PokemonCard:hover .Details {
  opacity: 1;
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -30%);
  transition: 0.3s;
}
.PokemonCard:hover .DetailInfo {
  transition: 0.2s;
  opacity: 0;
  visibility: hidden;
}

@media (max-width: 1024px) {
  .PokemonCard {
      flex: 0 0 calc(91%/3);
      margin: 30px 1.5% 40px 1.5%;
  }
}

@media (max-width: 768px) {
  .PokemonCard:hover .Details {
      display: none;
  }

  .PokemonCard:hover .DetailInfo {
      transform: translate(0px, 0px);
      visibility: visible;
      opacity: 1;
  }
  .PokemonCard {
    flex: 0 0 46%;
    margin: 30px 2% 40px 2%;
  }
}

@media (max-width: 480px) {
  .PokemonCard {
    flex: 0 0 70%;
    margin: 30px 15% 40px 15%;
    padding: 10px;
  }
}

